<template>
  <div class="content">
    <van-search placeholder="" v-model="searchVal" @input="onSearch" />
    <div class="searchList" v-if="searchList"></div>
    <bscroll :menus="menusJson" @reduceNum="reduceBtn($event)" @addNum="addBtn($event)"></bscroll>
    <footerCar :carItem="item"/>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import Scroll from "@/components/scroll/scroll";
import bscroll from "@/components/scroll/linkage";
import footerCar from "@/components/scroll/footerCar";

export default {
  data() {
    return {
      menus: this.getRespone.menus,
      item: [],
      menusJson: [],
      searchVal: "",
      searchList: false
    };
  },
  mounted() {
    this.menusJson = this.menus;
  },
  methods: {
    /** */
    reduceBtn(params) {
      var currentIndex = params.index;
      var currentParentIdx = params.parentIndex;
      var meanJson = this.menus;
      meanJson.forEach((e, i) => {
        var meanData = e.data;
        if (currentParentIdx === i) {
          meanData.forEach((el, index) => {
            if (currentIndex === index) {
              if (el.num > 0) {
                --el.num;
                var jsonSearch = this.item.filter(function(item) {
                  return item.num > 0;
                });
                this.item = jsonSearch;
              } else {
                // http.toast("~");
                this.common_vant.openToast("~");
              }
            }
          });
        }
      });
    },
    addBtn(params) {
      var currentIndex = params.index;
      var currentParentIdx = params.parentIndex;
      var meanJson = this.menus;
      meanJson.forEach((e, i) => {
        var meanData = e.data;
        if (currentParentIdx === i) {
          meanData.forEach((el, index) => {
            if (currentIndex === index) {
              ++el.num;
              if (el.num <= 1) {
                this.item.push(el);
              }
            }
          });
        }
      });
    },
    /** */
    onSearch() {
      var searchVal = this.searchVal;
      var test = [];
      this.menus.forEach(e => {
        e.data.forEach(el => {
          if (el.name.indexOf(searchVal) >= 0) {
            this.menusJson = [];
            this.menusJson.push(e);
            test.push(e);
            this.searchList = true;
          }
        });
      });
      if (test == '') {
        this.searchVal = "";
        http.toast("" + searchVal);
      }
      // 
      if (this.searchVal === "") {
        this.menusJson = this.menus;
        var test = [];
        this.searchList = false;
      }
    }
  },
  components: {
    bscroll,
    footerCar
  }
};
</script>

<style lang="scss" scoped>
.content{
  @include wh(100%, 100%);
  position: absolute;
  right: 0;
  left: 0;
  overflow: hidden;
}
.searchList {
  z-index: 13;
  position: absolute;
  background-color: $fc;
}
</style>