<template>
  <div class="content">
    <transition name="toggle-cart">
      <section class="listContent" v-show="isShow">
        <div class="carList" v-for="(mean, i) in carItem" :key="i">
          <p>{{mean.name}}</p>
          <p>{{mean.price}}</p>
        </div>
      </section>
    </transition>
    <transition name="fade">
      <div class="screen_cover" @click="clickCar" v-show="isShow"></div>
    </transition>
    <div class="footerCar">
      <section class="cart_icon_container" @click="clickCar">
        <span class="cart_list_length">{{carItem.length}}</span>
        <van-icon name="shopping-cart" color="#fff" size="23" />
      </section>
      <div class="right">
        <div>{{allPrice}}</div>
        <div class="payBtn" @click="$router.go(-1)"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      allPrice:0
    };
  },
  mounted(){
    this.getAllPrice();
  },
  props: ["carItem"],
  methods: {
    clickCar() {
      this.isShow = !this.isShow;
    },
    getAllPrice(){
      var carItem = this.carItem;
      carItem.forEach(e =>{
        this.allPrice += parseInt(e.price)
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.listContent {
  @include wh(100%, auto);
  background-color: $fc;
  position: fixed;
  bottom: 2rem;
  padding-bottom: 0.8rem;
  z-index: 12;
  padding-bottom: 2rem;
  .carList {
    @include wh(100%, auto);
    @include sc(0.6rem, #333);
    @include fr(space-between);
    padding: 0.5rem;
  }
  
}
.screen_cover {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 11;
}
.footerCar {
  @include wh(100%, 3.5rem);
  @include sc(0.8rem, #fff);
  @include fr(space-between);
  line-height: 3.5rem;
  background-color: $fc;
  bottom: 0;
  position: fixed;
  z-index: 12;
  .cart_icon_container {
    display: flex;
    background-color: #f63557;
    padding: 0.4rem;
    border: 0.25rem solid $fc;
    border-radius: 50%;
    margin: 0rem 0.7rem 2rem 0.7rem;
    .cart_icon {
      @include wh(1rem, 1rem);
    }
    .cart_list_length {
      position: absolute;
      top: -1.1rem;
      left: 2.1rem;
      background-color: #ff461d;
      line-height: 1.3rem;
      text-align: center;
      border-radius: 50%;
      border: 0.025rem solid #ff461d;
      @include wh(1.3rem, 1.3rem);
      @include sc(0.9rem, #fff);
      font-family: Helvetica Neue, Tahoma, Arial;
    }
  }
  .right {
    flex: 1;
    @include fr(space-between);
    div{
      @include sc(0.9rem,#f63557);
      font-weight: bold;
    }
    .payBtn {
      @include wh(30%, 3.5rem);
      @include sc(0.9rem, #fff);
      background-color: #f63557;
      @include fr(center);
    }
  }
}
</style>