<template>
  <div class="cascad-menu" ref="cascadMenu">
    <scroll class="left-menu" :data="menus" ref="leftMenu">
      <div class="left-menu-container">
        <ul>
          <li
            class="left-item"
            ref="leftItem"
            :class="{'current': currentIndex === index}"
            @click="selectLeft(index, $event)"
            v-for="(menu, index) in menus"
            :key="index"
          >
            <p class="text">{{menu.name}}</p>
          </li>
        </ul>
      </div>
    </scroll>
    <scroll
      class="right-menu"
      :data="menus"
      ref="rightMenu"
      @scroll="scrollHeight"
      :listenScroll="true"
      :probeType="3"
    >
      <div class="right-menu-container">
        <ul>
          <li class="right-item" ref="rightItem" v-for="(menu, i) in menus" :key="i">
            <div class="title">{{menu.name}}</div>
            <ul>
              <li v-for="(item, j) in menu.data" :key="j">
                <div class="data-wrapper">
                  <div class="leftShop">
                    <img :src="item.img" />
                    <div class="data">
                      <p>{{item.name}}</p>
                      <p>{{item.price}}</p>
                    </div>
                  </div>
                  <div class="rightNum">
                    <div class="reduceNum">
                      <p @click="reduceNum(j,i)">-</p>
                    </div>
                    <input :value="item.num" readonly />
                    <div class="divSpan">
                      <p @click="addNum(j,i)">+</p>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </scroll>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import Scroll from "./scroll";
export default {
  data() {
    return {
      rightTops: [],
      scrollY: 0,
      leftScrollY: 0
    };
  },
  props: {
    menus: {
      required: true,
      type: Array,
      default() {
        return [];
      }
    }
  },
  computed: {
    currentIndex() {
      const { scrollY, rightTops } = this;
      let index = rightTops.findIndex((height, index) => {
        return scrollY >= rightTops[index] && scrollY < rightTops[index + 1];
      });
      if (scrollY > rightTops[index]) {
        index++;
      }
      return index;
    }
  },
  created() {
    this.$nextTick(() => {
      this._calculateHeight();
    });
  },
  methods: {
    reduceNum(index, parentIndex) {
      var params = {
        index: index,
        parentIndex: parentIndex
      };
      this.$emit("reduceNum", params);
    },
    addNum(index, parentIndex) {
      var params = {
        index: index,
        parentIndex: parentIndex
      };
      this.$emit("addNum", params);
    },
    selectLeft(index, event) {
      if (!event._constructed) {
        return;
      }
      let rightItem = this.$refs.rightItem;
      let el = rightItem[index];
      this.$refs.rightMenu.scrollToElement(el, 300);
    },
    scrollHeight(pos) {
      this.scrollY = Math.abs(Math.round(pos.y));
    },
    _calculateHeight() {
      let lis = this.$refs.rightItem;
      let height = 0;
      this.rightTops.push(height);
      Array.prototype.slice.call(lis).forEach(li => {
        height += li.clientHeight;
        this.rightTops.push(height - 90);
      });
    }
  },
  components: {
    Scroll
  }
};
</script>

<style lang="scss" scoped>
.cascad-menu {
  @include sc(0.8rem, $fColor);
  display: flex;
  position: absolute;
  top: 3rem;
  bottom: 3.6rem;
  width: 100%;
  overflow: hidden;
  z-index: 2;
  .left-menu {
    width: 80px;
    text-align: center;
    background: #f3f5f7;
    .left-item {
      height: 54px;
      .text {
        width: 100%;
        line-height: 54px;
      }
    }
    .current {
      background: #fff;
    }
  }
  .right-menu {
    flex: 1;
    .right-item {
      height: 100%;
      padding-bottom: 5px;
      background-color: #fff;
      .title {
        background-color: rgba(252, 252, 252, 0.788);
        padding: 5px 10px;
        text-align: left;
      }
      .data-wrapper {
        display: flex;
        justify-content: space-between;
        padding: 10px;
        border-bottom: 0.05rem solid $F5;
        .leftShop {
          @include fr(center);
          img {
           @include wh(2.5rem,2.5rem);
           margin-right: 0.5rem;
           border-radius: 5rem;
          }
          p {
            @include sc(0.8rem,$line);
            text-align: left;
          }
          p:first-child{
            @include sc(0.9rem,$fColor);
            font-weight: bold;
          }
        }
        .rightNum {
          @include fr(center);
          @include wh(30%, auto);
          input {
            @include wh(40%, auto);
            text-align: center;
            font-weight: bold;
          }
          .divSpan{
            @include wh(1.5rem,1.5rem);
            @include fr(center);
            background-color: #F63557;
            border-radius: 5rem;
            p{
              @include sc(1.2rem,$fc);
              margin-top: -0.1rem;
            }
          }
          .reduceNum{
            @include wh(1.5rem,1.5rem);
            @include fr(center);
            border:0.05rem solid #F63557;
            border-radius: 5rem;
            p{
              @include sc(1.5rem,#F63557);
              margin-top: -0.1rem;
            }
          }
        }
      }
    }
  }
}
.topDiv {
  line-height: 50px;
  background-color: #333;
  bottom: 0px;
}
</style>
